import { Component, Input } from '@angular/core';
import { IOperation } from '@c8y/client';
import { BulkOperationDetailsService } from '@c8y/ngx-components/operations/bulk-operations-list';

@Component({
  selector: 'c8y-single-operation-details',
  templateUrl: './single-operation-details.component.html'
})
export class SingleOperationDetailsComponent {
  @Input() operation: Partial<IOperation>;
  @Input() readOnly: boolean = false;

  constructor(private bulkOperationDetailsService: BulkOperationDetailsService) {}

  openBulkOperationDetails() {
    this.bulkOperationDetailsService.openDetails(this.operation.bulkOperationId);
  }
}
