import { Injectable } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { IOperation, OperationService } from '@c8y/client';
import { SingleOperationModalComponent } from './single-operation-modal.component';

@Injectable()
export class SingleOperationDetailsService {
  constructor(private operationService: OperationService, private modalService: BsModalService) {}

  /**
   * Opens a modal window with the details of given single operation.
   * @param operationOrOperationId Operation's object or id.
   * @param options Additional options:
   * - `collapsed`: boolean - whether the details pane should be initially collapsed
   * - `readOnly`: boolean - whether the details should be displayed in read-only mode
   */
  async openDetails(
    operationOrOperationId: IOperation | string | number,
    options?: { collapsed?: boolean; readOnly?: boolean }
  ) {
    const operation: IOperation = (operationOrOperationId as IOperation).id
      ? (operationOrOperationId as IOperation)
      : (await this.operationService.detail(operationOrOperationId)).data;
    const initialState = { operation, ...options };

    this.modalService.show(SingleOperationModalComponent, {
      initialState,
      class: 'modal-lg'
    });
  }
}
