import { Component, Input } from '@angular/core';
import { IOperation } from '@c8y/client';
import { operationStatusClasses, operationStatusIcons } from '@c8y/ngx-components';
import { SingleOperationDetailsService } from './single-operation-details.service';

/**
 * Single operation component displays a single operation's status, description and creationTime.
 *
 * Example:
 *
 * ```html
 * <c8y-single-operation [operation]="operation"></c8y-single-operation>
 * ```
 */
@Component({
  selector: 'c8y-single-operation',
  templateUrl: './single-operation.component.html'
})
export class SingleOperationComponent {
  @Input() operation: IOperation;

  statusIcons: object;
  statusClasses: object;

  constructor(private singleOperationDetailsService: SingleOperationDetailsService) {}

  ngOnInit() {
    this.statusIcons = operationStatusIcons;
    this.statusClasses = operationStatusClasses;
  }

  openDetails() {
    this.singleOperationDetailsService.openDetails(this.operation);
  }
}
